import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { Row, Col } from 'antd';
import { Image } from '@uz/unitz-components-web/Image';

import { ctx } from '@vl/redata';
import gstyles from '@vl/gstyles';
import _ from 'lodash';
import styled from 'styled-components';

const CustomParapraph = styled.div`
  p > p {
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 16px;
    color: ${gstyles.colors.main};
  }
`;

const CustomItem = styled.div`
  .ant-row {
    &:nth-child(odd) {
      flex-direction: row;
      .content {
        text-align: left;
      }
    }
    &:nth-child(even) {
      flex-direction: row-reverse;
      .content {
        text-align: right;
      }
    }
  }
`;

const View10 = () => {
  return (
    <DIV className="content">
      <CustomItem>
        {ctx.get('sectionData.children', []).map((item, index) => {
          return (
            <Row gutter={24} className="mb-12 justify-center items-center" key={`item_${index}`}>
              <Col lg={12} sm={24}>
                <div>
                  <Image
                    className="object-cover"
                    size="cover"
                    src={ctx.apply('ctf.findImage', item, 'image', '')}
                    alt={item.id}
                    preview={false}
                  />
                </div>
              </Col>
              <Col className="content" lg={12} sm={24}>
                <div>
                  <h2 className="mb-8 text-4xl font-semibold text-main">{_.get(item, 'shortText', '')}</h2>
                  <CustomParapraph>
                    {ctx.apply('ctf.renderHTMLText', { name: _.get(item, 'name', '') }, 'longText.longText', {
                      className: 'text-ellipsis-6',
                      as: 'p',
                    })}
                  </CustomParapraph>
                </div>
              </Col>
            </Row>
          );
        })}
      </CustomItem>
    </DIV>
  );
};

export default View10;
