import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { ctx } from '@vl/redata';
import { Image } from '@uz/unitz-components-web/Image';
import { Row, Col, Button } from 'antd';
import { Link } from '@uz/unitz-components-web/Link';
import _ from 'lodash';

const View8 = () => {
  return (
    <DIV className="banner">
      <div className="relative content">
        <div className="absolute z-10 w-full top-24">
          <div className="wrapper app-row">
            <div className="w-1/2">
              <h1 style={{ lineHeight: '60px' }} className="text-5xl font-bold tracking-wider text-white">
                {ctx.get('sectionData.section.shortText')}
              </h1>
              <div className="mb-8">
                <p className="text-xl mb-0 tracking-wider text-white">
                  {ctx.apply('ctf.renderRichText', { name: ctx.get('sectionData.section.name') }, 'richText', null)}
                </p>
              </div>
              <Row>
                {ctx.get('sectionData.children', []).map((item, index) => {
                  return (
                    <Col key={`item_${index}`} span={10}>
                      <Link to={_.get(item, 'linkHref')}>
                        <Button className="w-full" type="primary" size="large">
                          {_.get(item, 'shortText', '')}
                        </Button>
                      </Link>
                    </Col>
                  );
                })}
              </Row>
            </div>
          </div>
        </div>
        <Image
          size="cover"
          src={ctx.apply('ctf.findImage', ctx.get('sectionData.section'), 'image', '')}
          alt="banner"
          className="w-full"
        />
      </div>
    </DIV>
  );
};

export default View8;
