import React from 'react';

import DIV from '@vl/redata/DIV.macro';
import { Row, Col, Space } from 'antd';
import { ctx } from '@vl/redata';
import _ from 'lodash';
import styled from 'styled-components';
import gstyles from '@vl/gstyles';
import { Link } from '@uz/unitz-components-web/Link';
import { Image } from '@uz/unitz-components-web/Image';

const CustomParagraph = styled.div`
  div {
    p {
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 0;
      text-align: left;
      color: ${gstyles.colors.main};
    }
  }
`;

const View10 = () => {
  return (
    <DIV className="guideline">
      <div className="mb-32">
        <Row gutter={24}>
          {ctx.get('sectionData.children', []).map((item) => {
            return (
              <Col key={item.id} className="mb-3 gutter-row sm:mb-0 " lg={8} sm={24} md={12}>
                <div className="h-full px-4 py-10 text-center border shadow-md bg-white500 rounded-xl">
                  <div className="mb-6 w-10 h-10">
                    <Image size="cover" src={ctx.apply('ctf.findImage', item, 'image', '')} alt="logo" />
                  </div>
                  <h3 className="text-left font-semibold text-2xl text-main">{_.get(item, 'shortText', '')}</h3>

                  <CustomParagraph>
                    {ctx.apply('ctf.renderHTMLText', { name: item.name }, 'detailText.detailText', {
                      className: 'text-ellipsis-1 mb-6',
                      as: 'div',
                    })}
                  </CustomParagraph>

                  <div className="text-left">
                    <Link className="text-left" to={item.linkHref}>
                      <Space>
                        <h5 className="mb-0 text-base font-semibold text-brandb500">{_.get(item, 'action', '')}</h5>
                        {gstyles.icons({ name: 'arrow-right2', size: 20, fill: gstyles.colors.brandb500 })}
                      </Space>
                    </Link>
                  </div>
                </div>
              </Col>
            );
          })}
        </Row>
      </div>
    </DIV>
  );
};

export default View10;
